@mixin border-radius($tl, $tr, $br, $bl) {
  border-radius: $tl $tr $br $bl;
}
@mixin padding-start($value) {
    padding-inline-start: $value;
}
@mixin padding-end($value) {
    padding-inline-end: $value;
}

.section-static-newsletter-popup {
	.popup-inner {
		padding: 0;
	}
	.flex {
		margin: 0;
		align-items: center;
		>div {
			padding: 0;
		}
	}
	.block-image {
		.image__style {
			@include border-radius(0,0,0,0);
		}
		img {
			@include border-radius(0,0,0,0);
		}
	}
	.block-html {
		p {
			margin: 16px 0;
		}
	}
	.block-paragraph {
		.text-1 {
			margin-bottom: 8px;
		}
		.text-2 {
			margin-bottom: 8px;
		}
	}
	.countdown {
		.countdown-item:not(:first-child):before {
			@include padding-end(6px);
		}
		.countdown-item:first-child {
			@include padding-start(0);
		}
		.countdown-item:after {
			@include padding-start(4px);
			font-size: 10px;
		}
	}
	.countdown-item {
		@include padding-start(4px);
	}
	.block-form {
		input {
			height: 46px;
			@include border-radius(var(--border-radius), 0, 0, var(--border-radius));
		}
		button {
			flex: 0 0 46px;
			height: 46px;
			padding: 0;
			@include border-radius(0, var(--border-radius), var(--border-radius), 0);
			span {
				display: flex;
				align-items: center;
				justify-content: center;
			}
			svg {
				width: 20px;
				height: 20px;
			}
		}
	}
}

// change option admin
div[data-theme-editor] .popup-gl.active {
	.popup-inner {
		opacity: 1;
		visibility: visible;
	}
	.popup-layer {
		opacity: 1;
		visibility: visible;
	}
}
// end change option admin
